import React, { Component } from 'react'
import config from '../config'
export default class GirlImage extends Component {
 
  render() {
    const cmts= this.props.img.comment ? this.props.img.comment.map(
        comment => <p><span className="font-weight-bold"> {comment.createdBy.username} </span> : {comment.content}</p>) : ""
    return (
      <div className="girl-image">
        <img 
        className="img-fluid"
        src={config.rootPath + this.props.img.imageUrl} alt={this.props.img.title}/>
        {console.log(config.rootPath + this.props.img )}
        <h4>{this.props.img.title}</h4>
        <p>{this.props.img.description}</p>
        {cmts}
      </div>
    )
  }
}
