import React, { Component } from 'react'
import MainContent from '../components/MainContent';
import NavBar from '../components/NavBar';
import axios from '../axios'
export default class HomeScrenn extends Component {
    state = {
        images : [],
        displayedImages: [],
        searching: ""
      };
      componentDidMount() {
        axios
             .get('/api/images')
             .then(data => {
               console.log(data.data);
               setTimeout(() => {
                this.setState({
                  images : data.data,
                 });
               }, 1000);          
             })
             .catch(err => console.log(err))
      }

      _onSearchChanged = text => this.setState({searching : text});
  render() {
    const displayedImages = this.state.images.filter(img => 
      img.title.includes(this.state.searching) || 
      img.description.includes(this.state.searching)
    );
    return (
      <div>
        <NavBar onSearchChanged = {this._onSearchChanged} 
                  onLogin = {this.props.onLogin}
                  username = {this.props.username}/>
          <MainContent images={displayedImages}/>
      </div>
    )
  }
}
